import React from 'react';
import { FilterBy } from '../../types/FilterBy';

interface Props {
  filterTodos: (selection: string) => void,
  query: string,
  changeQuery: (value: string) => void,
  resetQuery: () => void,
}

export const TodoFilter: React.FC<Props> = React.memo(({
  filterTodos,
  query,
  changeQuery,
  resetQuery,
}) => (
  <form className="field has-addons">
    <p className="control">
      <span className="select">
        <select
          data-cy="statusSelect"
          onChange={(event) => filterTodos(event.target.value)}
        >
          <option value={FilterBy.All}>All</option>
          <option value={FilterBy.Active}>Active</option>
          <option value={FilterBy.Completed}>Completed</option>
        </select>
      </span>
    </p>

    <p className="control is-expanded has-icons-left has-icons-right">
      <input
        data-cy="searchInput"
        type="text"
        className="input"
        placeholder="Search..."
        value={query}
        onChange={(event) => changeQuery(event.target.value)}
      />
      <span className="icon is-left">
        <i className="fas fa-magnifying-glass" />
      </span>

      {query && (
        <span className="icon is-right" style={{ pointerEvents: 'all' }}>
          {/* eslint-disable-next-line jsx-a11y/control-has-associated-label */}
          <button
            data-cy="clearSearchButton"
            type="button"
            className="delete"
            onClick={resetQuery}
          />
        </span>
      )}
    </p>
  </form>
));
